			<p>Anyway, another tool I'm enjoying keeping up with the latest developments and techniques in is <b>Laravel</b>. I jumped in at 5.1 and have been holding on ever since. I've put together a RESTful API for a single noun based on 5.8 with both Vue and React components on the frontend, linked to <a href="https://github.com/FullStackDev-UK/laravel58">here</a>. This was a research endevour to explore which framework I'd go with for a service I'm re-designing. I ended up going with Vue. The app can consume <b>JSON</b> from either a static file in a serverless situation (exactly like this page's project list, on the <router-link to="/">home</router-link> page), as it is at the moment, or JSON returned from an <b>API</b>, from either a local server or, with <b>Passport/JWT</b>, a remote one.</p>

